
/*--------------------------------------------------------------
## Footer
--------------------------------------------------------------*/


.site-footer {
	position: relative;

	h2 {
		margin-bottom:0.5em;
	}
	
	a {
		color:var(--bleu);
		text-decoration: none;
		transition-duration: 0.3s;
		&:hover, &:focus {
			color:var(--bleu-fonce);
			text-decoration: underline;
		}
		br {
			display: none;
		}
	}

	ul {
		list-style: none;
		padding:0;
		margin:0;
		li {
			line-height: 30px;
		}
	}

	.backtotop {
		align-items: center;
		background-color:var(--bleu);
		border-radius:100%;
		display: flex;
		justify-content: center;
		height:30px;
		position: absolute;
		right:20px;
		top : -40px;
		width:30px;
		z-index:1;

		&:hover, &:focus {
			background-color:var(--bleu-fonce);
		}

		svg {
			margin-left:-1px;
			path {
				fill:var(--blanc);
			}
		}
	}

	.menus-footer, .coordonnees-boutique {
		padding:2rem var(--padding-x);

	}
	.logo-footer {
		text-align: center;
	}
	a.custom-logo-link {
		display: inline-block;
		max-width:200px;
		margin-bottom:1rem;
	}
	.coordonnees-boutique {
		background-color:var(--bleu-fonce);
		&, & a, & h2 {
			color:var(--blanc);
		}

		svg {
			margin-right:1ch;
		}
		
	}

	.social-links {
		padding-top:1rem;
		svg path {
			fill:currentColor;
		}
		svg.facebook {
			path {
				fill :var(--bleu-fonce);
			}

		}
	}

	.copyright {
		@include fs(17);
		padding:1rem var(--padding-x);
		
		&, a {
			color:var(--gris);
			
		}

		a {
			display:inline-block;
		}
	}
	.annee {
		&::after {
			content:'|';
			margin:0 1ch;
		}
	}
	.agence {
		display: block;
	}

	@include bp(sm) {
		.menus-footer, .coordonnees-boutique {
			display: grid;
			grid-template-columns: 1fr 1fr;
			column-gap: 1rem;
		} 
		.logo-footer {
			text-align: left;
		}
		a.custom-logo-link {
			margin-bottom: 0;
			margin-right:1rem;
		}
		.coordonnees-boutique {
			grid-template-rows: auto auto;
			h2 {
				grid-column: 1 / span 2;
			}
		}
		.social-links {
			align-self:end;
		}

		.agence {
			display: inline-block;
			&::before {
				content:'|';
				margin:0 1ch;
			}
		}
		.nav-footer-legal li {
			display: inline-block;
			&::before {
				content:'|';
				margin:0 1ch;
			}
			&:first-of-type::before {
				display: none;
			}
		}
	}

	@include bp(md) {
		.menus-footer {
			grid-template-columns: minmax(120px,250px) repeat(3,minmax(180px,1fr))
		}	
	}

	@include bp(xl) {
		display:grid;
		grid-template-columns: 1fr 450px;
		grid-template-rows : auto 50px;
		.coordonnees-boutique {
			grid-row : 1 / span 2;
			padding:2rem 1rem;
		}
		.copyright {
			grid-row-start:2;
			grid-column-start: 1;
			padding:0;
		}
		.copyright, .menus-footer {
			padding-left:var(--padding-x);
			padding-right:1rem;
		}
	}

	@media screen and (min-width:1400px) {
		grid-template-columns: 1fr 500px;
		grid-template-rows : auto 30px;
		.coordonnees-boutique {
			padding-left:2rem;
			padding-right:var(--padding-x);
		}
		.copyright {
			align-items: center;
			display: flex;
		}
		.nav-footer-legal li:first-of-type::before {
			display: inline-block;
		}
	}
}


